import React, { Component } from 'react';
import ViewDeviceRow from './ViewTransactionRow';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {
    getUser,
    unlinkDevice
  } from "../../store/api"

class ViewDeviceTable extends Component {
    state = {
        selectedDevice: "",
        showModal: false,
        showId: '',
        deviceData: '',
        accountData: [
            {   
                date: "29/10/2019",
                acctNo: "0082578934",
                phoneModel: "SM-G950F"
                
            },
            {   
                date: "03/11/2019",
                acctNo: "0982668466",
                phoneModel: "iPhone7.1"
                
            },
            {   
                date: "15/11/2019",
                acctNo: "0082578934",
                phoneModel: "Infinix X604"
                
            },
            {   
                date: "21/12/2019",
                acctNo: "0982668466",
                phoneModel: "SM-G925F"
                
            },
            {   
                date: "15/11/2019",
                acctNo: "0082578934",
                phoneModel: "Infinix X604"
                
            },
            {   
                date: "21/12/2019",
                acctNo: "0982668466",
                phoneModel: "SM-G925F"
                
            },
        ]
    }
    
    handleModal = (userID, deviceID) => {
        const { showModal} = this.state;
        this.setState({
            showModal: !showModal,
            selectedDevice: userID,
            showId: deviceID
        })
      
    } 
  
    handleUnlinkDevice = (data) => {
        const { selectedDevice, showId} = this.state
        data = {
          deviceID: showId,
          userID: selectedDevice,
        }
        console.log(data)  
        this.props.unlinkDevice(data)
        this.setState({
            showModal: false 
        })
    }
    render() {
        const { showModal } = this.state;
        const { getOneUser} = this.props;
        const userContent = [...[getOneUser]]
        const account = userContent.map((table, key) => 
            <ViewDeviceRow 
                value={table}
                key={key}
                handleModal={this.handleModal}
            />
        );
        return (
            <div>
                <table className="device__table">
                    <thead className="account__tableheader">
                        <tr className="account__head">
                            <th className="account__first">Date</th>
                            <th className="account__first">User Id</th>
                            <th className="account__first">Phone Model</th>
                            <th className="account__first">&nbsp;</th>
                        </tr>
                    </thead>
                    <tbody>
                        {account}
                    </tbody>
                </table>
                {
                    showModal ?<div className='account__modal'>
                        <div className='account__modalcontent'>
                            <span className='account__close' onClick={this.handleModal}>
                            &times;
                            </span>
                            <div className="account__modaldisplay">
                                <div className="account__modaltext">Are you sure you want to unlink device?</div>
                                <div className="account__modalcover">
                                    <button className="account__yes" onClick={this.handleUnlinkDevice}>Yes</button>
                                    <button className="account__no" onClick={this.handleModal}>No</button>
                                </div>
                            </div>
                        </div>
                    </div>: null
                } 
            </div>
        )
    }
}

const mapStateToProps = state => ({
    getOneUser: state.stanbic.user
      
})
export default withRouter(
    connect(
        mapStateToProps,
        {
          getUser,
          unlinkDevice
        }
    )(ViewDeviceTable)
)