import React, { Component } from 'react';
import Logo from '../../images/Bitmap.svg';

export class Login extends Component {
    render() {
        return (
            <div className="login">
                <div className="login__cover">
                    <div className="login__image">
                        <img src={Logo} alt="Logo" className="login__logo" />
                    </div>
                    <form className="login__form">
                        <div className="login__formcover">
                            <label htmlFor="email" className="login__label">Email Address</label>
                            <input type="email" name="email"  placeholder="chidinmaanuigbo@gmail.com" className="login__input" onChange={this.handleChange} required/>
                        </div>
                        <div>
                            <label htmlFor="password" className="login__label">Password</label>
                            <input type="password" name="password"  placeholder="........." className="login__input" onChange={this.handleChange} required/>
                        </div>
                        <div className="login__forgot">Forgot Your Password?</div>
                        <div className="login__btn">
                            <button className="login__sign">Sign In</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Login
