import React, { Component}  from 'react';
import { withRouter } from 'react-router-dom';
import Spinner from '../layout/Spinner';
import { connect } from 'react-redux';
import {
    pushNotification,
    pushNotifications
} from "../../store/api"
import Navbar from '../layout/Navbar';
import Cancel from '../../images/Cancel.svg';
import Sidebar from '../layout/Sidebar';
import Back from '../../images/stanback.svg';

class NewPush extends Component {
    state = {
        loading: false,
        notifications: {
            title: "",
            destination: [],
            message: ""
        }
    }
    handleClick = () => {
        this.props.history.push('/pushnotification')
    }
    handleChange = e => {
        console.log(e.target.value)
        const { notifications } = this.state;
        this.setState({
            notifications: {
                ...notifications,
                [e.target.name]: e.target.value
            }
        });
    };
   
    handlePushNotification = e => {
        this.setState({ loading: true })
        e.preventDefault()
        const { notifications} = this.state;
        const data = {
            key: '911776E837731A387DE2DE481CAE5A7FA9665FE38174D17189A9F94AB9',
            title: notifications.title,
            destination: notifications.destination,
            message: notifications.message
        }
        console.log(data)
        this.props.pushNotification(data).then(() => setTimeout(() => {
            this.setState({
                loading: false
            })
        }, 1000))
       
       
    }
    handlePushNotifications = e => {
        this.setState({ loading: true })
        e.preventDefault()
        const { notifications} = this.state;
        const data = {
            key: '911776E837731A387DE2DE481CAE5A7FA9665FE38174D17189A9F94AB9',
            title: notifications.title,
            message: notifications.message
        }
        console.log(data)
        this.props.pushNotifications(data).then(() => setTimeout(() => {
            this.setState({
                loading: false
            })
        }, 1000))
        
    }
    render () {
        const {loading} = this.state;
        if(loading) return <Spinner />  
        return (
            <div>
                <Sidebar />
                <div className="push">
                <Navbar heading="Create new notification" />
                <div className="cards__cover">
                    <div className="push__notify" onClick={this.handleClick}>
                        <img src={Back} alt='Back' />
                        <div className="device__back">Back</div>
                    </div> 
                        <div className="push__coverage">
                            <img src={Cancel} alt="Cancel" className="push__cancel" onClick={this.handleClick}/>
                            <div className="push__create">Create a new push notification for your users</div>
                            <form className="push__form" onSubmit={this.handlePushNotification}>
                                <div className="push__container">
                                    <label htmlFor="title" className="push__label">Notification Title</label>
                                    <input type="text" name="title" placeholder="New offer for you!"className="push__input" onChange={this.handleChange} required/>
                                </div>
                                <div className="push__container">
                                    <label htmlFor="message" className="push__label">Notification Text</label>
                                    <textarea name="message" placeholder="Our wide range of personal business and banking solutions let you spend more time living and less time banking" className="push__input" onChange={this.handleChange} required/>
                                </div>
                                <div className="push__container">
                                    <label htmlFor="destination" className="push__label">Recipient(User Id)</label>
                                    <input type="text" name="destination" placeholder="515716, 526477, 876478, 84636" className="push__input" onChange={this.handleChange} />
                                </div>
                                <div className="push__container">
                                    <input type="checkbox" placeholder="Push notifications" className="push__input" onChange={this.handlePushNotifications} />
                                    <label htmlFor="link" className="push__label" id="push__notify">Push notifications to all users</label>
                                </div>
                                <div className="push__contained"> 
                                    <button className="push__push" >Push</button>
                                    <span className="push__span">or</span> 
                                    <button className="push__cancelbtn" onClick={this.handleClick}>Cancel</button>
                                </div>
                            </form>
                        </div>
                </div>
                </div>
            </div>
        )
    }
}

export default withRouter(
    connect(
       null,
        {
           pushNotification,
           pushNotifications
        }
    )(NewPush)
)