import React from 'react';
import { withRouter } from 'react-router-dom';

const ManageAdvertCardItem = (props) => {
    return (
        <div className="cards__cardItem" onClick={props.displayAdvert}>
            <div className="cards__cardCover" key={props._id}>
                <img src={props.icon} alt="Logo" className="cards__cards"/>
                <img src={props.dots} alt="Dots" className="cards__dots" />
                <div className="cards__info">
                    <div className="cards__subheader">{props.title}</div>
                    <div className="cards__subtext">{props.info}</div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(ManageAdvertCardItem)
