import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Spinner from '../layout/Spinner';
import { connect } from 'react-redux';
import {
    getUsers,
    getUser
  } from "../../store/api"

import AccountRow from './AccountRow';

class AccountTable extends Component {
    state = {
        loading: false,
        accountData: [
            {
                date: "29/10/2019",
                name: "Chidinma Anuigbo",
                userId: "51576",
                phoneModel: "SM-G950F"
            },
            {
                date: "03/11/2019",
                name: "Peter Abu",
                userId: "112434",
                phoneModel: "iPhone7.1"
            },
            {
                date: "05/11/2019",
                name: "Anita Okoro",
                userId: "238576",
                phoneModel: "Infinix X604"
            },
            {
                date: "21/12/2019",
                name: "Zion Ibitoye",
                userId: "463293",
                phoneModel: "SM-G950F"
            },
        ]
    }
    componentDidMount = () => {
        this.setState({ loading: true })
        this.props.getUsers()
    }

    componentWillReceiveProps(nextProps) {
        if((this.state.loading) && (this.props.getUsers !== null)) {
            this.setState({
                loading: false
            })
        }
    }
    
    handleModal = (data) => {
        const { showModal} = this.state;
        this.setState({
            showModal: !showModal,
            selectedDevice: data
        })
        console.log(data)
        this.props.history.push('/viewdevice')
    }
    handleDisplayUser = userID => {
        console.log(userID)
        this.props.getUser(userID).then(() => {
         this.props.history.push(`/viewdevice`)
        })
     }
    render() {
        const { getAllUsers} = this.props;
        const { loading} = this.state;
        const account = getAllUsers.map((table, key) => 
            <AccountRow 
                value={table}
                key={table._id}
                handleDisplayUser={this.handleDisplayUser}
            />
        );
        if(loading) return <Spinner />  
        return (
            <div>
                <table className="account__table">
                    <thead className="account__tableheader">
                        <tr className="account__head">
                            <th className="account__first">Date</th>
                            <th className="account__first">User Id</th>
                            <th className="account__first">Phone Model</th>
                            <th className="account__first">&nbsp;</th>
                        </tr>
                    </thead>
                    <tbody>
                        {account}
                    </tbody>
                </table>
                
            </div>
        )
    }
}

const mapStateToProps = state => ({
    getAllUsers: state.stanbic.users
})
export default withRouter(
    connect(
        mapStateToProps,
        {
          getUsers,
          getUser
        }
    )(AccountTable)
)
  
