import React, { Fragment, Component} from 'react';
import { Link, withRouter } from 'react-router-dom';
import Navbar from '../layout/Navbar';
import ViewDeviceTable from './ViewTransactionTable';
import { connect } from 'react-redux';
import {
    getUser
} from "../../store/api"
import Sidebar from '../layout/Sidebar';
import Download from '../../images/download.svg';

class NewPush extends Component {
    handleClick = () => {
        this.props.history.push('/transaction')
    }
    render () {
        const { getOneUser} = this.props;
        const userContent = [...[getOneUser]]
        const account = userContent.map((table, key) => 
           <Fragment key={key}>
               <span><img src={`data:image/png;base64,${table.profilePicture}`} alt="UserImage"  className="account__spaces"/></span>
                <div className="account__userid">{table.userID}</div>
           </Fragment>
        );
        return (
            <div>
                <Sidebar />
                <div className="push">
                <Navbar heading="View Transactions"/>
                <div className="push__cover">
                    <div className="push__head">
                        <input 
                            type="search" 
                            placeholder="Search" 
                            name="notificationTitle"
                            className="push__inputsearch" 
                            onChange={this.handleChange}
                        />
                        <select className="push__filter">
                            <option>Filter by All</option>
                            <option>By Date</option>
                        </select>
                    </div>
                    <div >
                        <Link to="/newpost" className="push__header">
                            <img src={Download} alt="Download" />
                            Download
                        </Link>
                    </div>
                </div>
                <ViewDeviceTable />
               
            </div>
                
            </div>
        )
    }
}

const mapStateToProps = state => ({
    getOneUser: state.stanbic.user
      
})
export default withRouter(
    connect(
        mapStateToProps,
        {
          getUser
        }
    )(NewPush)
)