import React from 'react';
import moment from 'moment';

export default function (props) {
    return (
        <tr className="account__data" key={props.value.id}>
            <td className="account__detail">
                <div className="account__new">{props.value.title}</div>
                <div className="account__range">{props.value._id}</div>
            </td>
            <td className="account__detail" id="account__link">{props.value.message}</td>
            <td className="account__range">{moment(props.value.timestamp).format("ll")}</td>
        </tr>
    )
    
}