import React, { Component} from 'react';
import { withRouter } from 'react-router-dom';
import PushNotificationTable from './PushNotificationTable';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import {
    getAllNotifications,
} from "../../store/api"


class PushNotification extends Component {
    state = {
        notificationTitle: '',
        notifications: [],
        loading: false
    }
    handleChange = e => { 
        e.preventDefault()
        const notificationTitle = e.target.value
        const filteredData = this.props.getNotification.filter(obj => {
            return obj.title.toLowerCase().includes(notificationTitle.toLowerCase());
        })
        this.setState({
            notifications: filteredData
        })
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
          notifications: nextProps.getNotification
        });
      }
    render() {
        return (
            <div className="cards__cover">
                <div className="push__cover">
                    <div className="push__head">
                        <input 
                            type="search" 
                            placeholder="Search" 
                            name="notificationTitle"
                            className="push__inputsearch" 
                            onChange={this.handleChange}
                        />
                    </div>
                    <div >
                        <Link to="/newpost" className="push__header">New Notification</Link>
                    </div>
                </div>
                <PushNotificationTable getData={this.state.notifications}/>
               
            </div>
        )
    }
}
const mapStateToProps = state => ({
    getNotification: state.stanbic.getPushNotifications
})
export default withRouter(
    connect(
        mapStateToProps,
        {
           getAllNotifications
        }
    )(PushNotification)
)

