export const getUsers = (data) => async dispatch => {
    const token = 'Bearer a1f94dfe0b38c6fe98b68f754389c781f7836b0074cf61eee749ae1c989a218a';
    try {
    const response = await axios({
        method: 'GET',
        url: "https://stanbic-profile.nibse.com/api/admin/users",
        data: data,
        headers: {
            "Access-Control-Allow-Origin": "*",
            "Authorization" : token
        },
    })
    dispatch(getAllUsers(response.data.data.users))
    return response
    } 
    catch (err) {
        console.log(err)
    }
}
