import React, { Fragment, Component} from 'react';
import { withRouter } from 'react-router-dom';
import Navbar from '../layout/Navbar';
import ViewDeviceTable from './ViewDeviceTable';
import { connect } from 'react-redux';
import {
    getUser
} from "../../store/api"
import Sidebar from '../layout/Sidebar';
import UserIcon from '../../images/Device.png';
import Back from '../../images/stanback.svg';

class NewPush extends Component {
    handleClick = () => {
        this.props.history.push('/devicebinding')
    }
    render () {
        const { getOneUser} = this.props;
        const userContent = [...[getOneUser]]
        const account = userContent.map((table, key) => 
           <Fragment key={key}>
               <span><img src={`data:image/png;base64,${table.profilePicture}`} alt="UserImage"  className="account__spaces"/></span>
                <div className="account__userid">{table.userID}</div>
           </Fragment>
        );
        return (
            <div>
                <Sidebar />
                <div className="push">
                <Navbar heading="Device Binding"/>
                <div className="cards__cover">
                    <div className="push__notify" onClick={this.handleClick}>
                        <img src={Back} alt='Back' />
                        <div className="device__back">Back</div>
                    </div>
                    <div className="device__cover">
                        <div className="account__full">
                            <img src={UserIcon} alt="User" className="device__image" />
                            {account}
                        </div>
                        <ViewDeviceTable />
                    </div>
                    
                </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    getOneUser: state.stanbic.user
      
})
export default withRouter(
    connect(
        mapStateToProps,
        {
          getUser
        }
    )(NewPush)
)