import React from 'react';
import { withRouter } from 'react-router-dom';

const CardItem = (props) => {
    return (
        <div className="cards__cardItem">
            <div className="cards__cardCover" onClick={props.gotoRoute}>
                <img src={props.icon} alt="Logo" className="cards__image"/>
                <div className="cards__info">
                    <div className="cards__mainheader">{props.title}</div>
                    <div className="cards__maininfo">{props.info}</div>
                    <div className="cards__select" onClick={props.gotoRoute}>{props.select}</div>
                </div>
            </div>
        </div>
    )
}
export default withRouter(CardItem)
