import React, { Component } from 'react';
import ManageAdvertCardItem from './ManageAdvertCardItem';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../layout/Spinner';
import {
    getAdverts,
    getAdvert
  } from "../../store/api"
import Dots from '../../images/Dots.png';

class ManageAdvertCards extends Component {
    state = {
        loading: false
    }
    componentDidMount() {
        this.setState({ loading: true })
        this.props.getAdverts()
        // this.props.getAdvert(id)
    }
    componentWillReceiveProps(nextProps) {
        if((this.state.loading) && (this.props.getAdverts !== null)) {
            this.setState({
                loading: false
            })
        }
    }
    handleDisplayAdvert = id => {
        this.props.getAdvert(id).then(() => {
            this.props.history.push(`/viewadvert`)
        })
     }
    render() {
        const { getOneAdvert } = this.props
        const { loading } = this.state;
        const viewCards = getOneAdvert && getOneAdvert.map((advert, key) => (
            <ManageAdvertCardItem
                dots={Dots}
                icon={advert.advertImage}
                title={advert.title}
                info={advert.text}
                key={key}
                displayAdvert={()=>this.handleDisplayAdvert(advert._id)}
            />
        ))
        if(loading) return <Spinner /> 
        return (
            <div className="cards__cover">
                <div className="cards__flex">
                    {viewCards}
                </div>
                
            </div>
        )
    }
}


const mapStateToProps = state => ({
    getOneAdvert: state.stanbic.adverts
})

export default withRouter(
    connect(
        mapStateToProps,
        {
            getAdverts,
            getAdvert
        }
    )(ManageAdvertCards)
)
