import React, { Component} from 'react'
import DeviceBindingTable from './DeviceBindingTable';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {
    searchDevices,
  } from "../../store/api"



class DeviceBinding extends Component {
    state = {
        userID: '',
        users: [],
    }
    handleChange = e => { 
        e.preventDefault()
        const userID = e.target.value
        this.props.searchDevices(userID)
    }
   
    render() {
        return (
            <div className="cards__cover">
                <div className="push__cover">
                    <div>
                        <input 
                            type="search" 
                            placeholder="Search" 
                            className="push__inputsearch"
                            name="userID"
                            onChange={this.handleChange}
                        />
                    </div>
                    <div className="push__head">&nbsp;</div>
                </div>
                {/* <div className="device__head">Device Binding</div> */}
                <DeviceBindingTable />
            </div>
        )
    }
}

const mapStateToProps = state => ({
    searchAllDevice: state.stanbic.searchDevice
})

export default withRouter(
    connect(
        mapStateToProps,
        {
            searchDevices
        }
    )(DeviceBinding)
)
