import React, { Component} from 'react';
import { withRouter } from 'react-router-dom';

class DeviceBindingRow extends Component {
    render () {
        return (
            <tr className="account__data" key={this.props.value._id}>
            <td className="account__detail">
                <span><img src={`data:image/png;base64,${this.props.value.profilePicture}`} alt="UserImage"  className="account__space"/></span>
            </td>
            <td className="account__detail">{this.props.value.userID}</td>
            <td className="account__detail">{this.props.value.device.length}</td>
            <td className="account__detail">
                <div className="account__view" onClick={() => this.props.displayUser(this.props.value.userID)}>View History</div>
            </td>
        </tr>
        )
    }
    
}
export default withRouter(DeviceBindingRow)