import React, { Fragment,Component} from 'react';
import { withRouter } from 'react-router-dom';
import moment from 'moment';


const HandleDeviceRow = (props) => {
    const device = props.value.device 
    // const length = props.value.device.length
    return device ? device.map(data=> <tr className="account__data" key={data.deviceID}>
        <td>&nbsp;</td>
        <td>&nbsp;</td>
        <td className="account__detail">{data.deviceID}</td>
        <td className="account__detail">
            <div className="account__unlink" onClick={() => props.handleModal(props.value.userID, data.deviceID)}>Unlink</div>
        </td>
    </tr> 
       
    ): null
}

class ViewDeviceRow extends Component {
//     state = {
//         value: ''
//     }
//    componentWillReceiveProps(nextProps) {
//     console.log(nextProps.value)
//        this.setState({
//             value: nextProps.value
//        })
       
//    }
    handleDevice = () => {
        console.log('hey')
        this.props.history.push("/viewdevice")
    }
    render () {
        return (
            <tr className="account__data" key={this.props.value.userID}>
                <td className="account__detail">{moment(this.props.value.createdAt).format("ll")}</td>
                <td className="account__detail">{this.props.value.userID}</td>
                <Fragment>
                    <HandleDeviceRow 
                        value={this.props.value}
                        handleModal={this.props.handleModal}
                        key={this.props.value.device}
                    />
                </Fragment>
                {/* <td className="account__detail" ><div className="account__unlink">Unlink</div></td> */}
            </tr>
        )
    }
    
}
export default withRouter(ViewDeviceRow)