import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Navbar from '../layout/Navbar';
import Cancel from '../../images/Cancel.svg';
import Sidebar from '../layout/Sidebar';
import Back from '../../images/stanback.svg';

class AdminNew extends Component {
    handleClick = () => {
        this.props.history.push('/admin')
    }
    render () {
        return (
            <div>
            <Sidebar />
            <div className="push">
        <Navbar heading="New Admin"/>
        <div className="cards__cover">
            <div className="push__notify" onClick={this.handleClick}>
                <img src={Back} alt='Back' />
                <div className="device__back">Back</div>
            </div>
            <div className="push__coverage">
                <img src={Cancel} alt="Cancel" className="push__cancel" onClick={this.handleClick}/>
                <div className="push__create">Create a new Admin</div>
                <form>
                    <div className="push__container">
                        <input type="file" placeholder="Anuigbo Chidinma" className="push__input" id="push__file"/>
                    </div>
                    <div className="push__container">
                        <label htmlFor="name" className="push__label">Full Name</label>
                        <input type="text" placeholder="Anuigbo Chidinma" className="push__input"/>
                    </div>
                    <div className="push__container">
                        <label htmlFor="email" className="push__label">Email Address</label>
                        <input type="text" placeholder="Chidinma@swipemax.com" className="push__input"/>
                    </div>
                    <div className="push__contained"> 
                        <button className="push__push">Push</button>
                        <span className="push__span">or</span> 
                        <button className="push__cancelbtn" onClick={this.handleClick}>Cancel</button>
                    </div>
                </form>
            </div>
        </div>
        </div>
    </div>
        )
    }
} 
    

export default withRouter(AdminNew);