import React from 'react';
import moment from 'moment';

export default function (props) {
    return (
        <tr className="account__data">
            <td className="account__detail">{moment(props.value.createdAt).format("ll")}</td>
            <td className="account__detail">{props.value.userID}</td>
            <td className="account__detail">{props.value.device[0] && props.value.device.map((device)=> device.deviceName).join(',')}</td>
            <td className="account__detail" >
                <div className="account__unlink" onClick={() => props.handleDisplayUser(props.value.userID)}>Intiate</div>
            </td>
        </tr>
    )
    
}