import React, { Component } from 'react';
import Account from '../account/Account'
import CardItem from './CardItem';
import { withRouter } from 'react-router-dom';
import PushNotification from '../../images/deep.png';
import DeviceBinding from '../../images/orange.png';
import Adverts from '../../images/purple.png';
import AdminManagement from '../../images/admin.png';

class Cards extends Component {
    handleRoute = (route) => {
        this.props.history.push(route)
    }
    render() {
        return (
            <div className="cards__cover">
                <div className="cards__header">What Would You Like To Do Today?</div>
                <div className="cards__flex">
                    <CardItem
                        icon={PushNotification}
                        title="Push Notification"
                        info="Inform users about ongoing deals"
                        select="Select"
                        gotoRoute={()=> this.handleRoute('/pushnotification')}
                    />
                    <CardItem
                        icon={DeviceBinding}
                        info="Manage Users and their devices"
                        select="Select"
                        title="Device Binding"
                        gotoRoute={()=> this.handleRoute('/devicebinding')}
                    />
                     <CardItem
                        icon={Adverts}
                        info="Manage Users and their devices"
                        select="Select"
                        title="Manage Adverts"
                        gotoRoute={()=> this.handleRoute('/manageadvert')}
                    />
                    <CardItem
                        icon={AdminManagement}
                        title="Admin Management"
                        info="Manage Users and their devices"
                        select="Select"
                        gotoRoute={()=> this.handleRoute('/admin')}
                    />
                </div>
                
                <Account />
            </div>
        )
    }
}

export default withRouter(Cards);
