import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import sweetalert from 'sweetalert';
import Spinner from '../layout/Spinner';
import {
    createAdvert,
} from "../../store/api"
import Navbar from '../layout/Navbar';
import Cancel from '../../images/Cancel.svg';
import Sidebar from '../layout/Sidebar';
import Back from '../../images/stanback.svg';

class Manage extends Component {
    state = {
        loading: false,
        adverts: {
            title: "",
            link: "",
            text: "",
            module: "",
            image: {
                advertImage: "",
                selectedFile: null,
                imagePreviewUrl: null
            }, 
        }
    }
    handleChange = e => {
        console.log(e.target.value)
        const { adverts } = this.state;
        this.setState({
            adverts: {
                ...adverts,
                [e.target.name]: e.target.value
            }
        });
    };
    onImgLoad = ({target:img}) => {
        this.setState({
            dimensions:{
                height:img.offsetHeight,
                width:img.offsetWidth
            }
        })

        // },() =>  this.displayImageSize())
    }
    displayImageSize = () => {
        const { dimensions } = this.state;
        if( (dimensions.height < 700) || (dimensions.width < 350)) {
            sweetalert ("Image width or height is less than 350 by 700px")
            this.setState({
                adverts: {
                    ...this.state.adverts,
                    image: {
                        ...this.state.image,
                        advertImage: '',
                        selectedFile: null,
                        imagePreviewUrl: null
                    }
                }
            })
           
        } else if ((dimensions.height > 700) || (dimensions.width > 350)) {
            sweetalert ("Image width or height is greater than 350 by 700px")
            this.setState({
                adverts: {
                    ...this.state.adverts,
                    image: {
                        ...this.state.image,
                        advertImage: '',
                        selectedFile: null,
                        imagePreviewUrl: null
                    }
                }
            })
        }
    }
    handleImageChange = (e) => {
        const imageMaxSize = 2000000 // bytes
        let file = e.target.files[0]
        const acceptedFileTypes = 'image/x-png, image/png, image/jpg, image/jpeg, image/gif'
        const acceptedFileTypesArray = acceptedFileTypes.split(',').map((item) => { return item.trim() })
        const verifyFile = (files) => {
          if (files) {
            const currentFile = file
            const currentFileType = currentFile.type
            const currentFileSize = currentFile.size
            if (currentFileSize > imageMaxSize) {
              alert('This file is not allowed. ' + Math.round(currentFileSize / 1000000) + ' Mb is too large, maximum allowed size is 1mb')
              return false
            }
            if (!acceptedFileTypesArray.includes(currentFileType)) {
              alert('This file is not allowed. Only images are allowed.')
              return false
            }
            return true
          }
        }
        if (file) {
          const isVerified = verifyFile(file)
          if (isVerified) {
            let reader = new FileReader()
            reader.onloadend = () => {
                this.setState(
                    {
                        adverts: {
                            ...this.state.adverts,
                            image: {
                                ...this.state.image,
                                advertImage: file.name,
                                selectedFile: file,
                                imagePreviewUrl: reader.result
                            }
                        }
                    }
                );
            //    console.log(adverts.image)
            //   setInputs(inputs => ({ ...inputs, image: file, imagePreviewUrl: reader.result }))
            }
            reader.readAsDataURL(file)
          }
        }
      }
    handleAdvertImage = e => {
        const { adverts} = this.state;
        // if(this.handleCheckImage()) {
            this.setState(
                {
                    adverts: {
                        ...this.state.adverts,
                        image: {
                            ...this.state.image,
                            advertImage: e.target.name,
                            selectedFile: e.target.files[0]
                        }
                    }
                }
            );
           console.log(adverts.image)
        // }
    };
    handleCreateAdvert =  e => {
        this.setState({ loading: true })
        e.preventDefault()
        const { adverts} = this.state;
        const data = new FormData();

        data.append("title", adverts.title);
        data.append("module", adverts.module);
        data.append("advertImage", adverts.image.selectedFile);
        data.append("text", adverts.text);
        data.append("link", adverts.link);

        console.log(this.state);
        console.log(data)
            
        this.props.createAdvert(data).then(() => setTimeout(() => {
            this.setState({
                loading: false
            })
        }, 1000))
    
    }
    handleClick = () => {
        this.props.history.push('/manageadvert')
    }
    render () {
        const { adverts, loading} =this.state
        if(loading) return <Spinner />  
        return (
            <div>
        <Sidebar />
        <div className="push">
        <Navbar heading="Create new advert" />
        <div className="cards__cover">
            <div className="push__notify" onClick={this.handleClick}>
                <img src={Back} alt='Back' />
                <div className="device__back">Back</div>
            </div>
            <div className="push__coverage">
                
                <img src={Cancel} alt="Cancel" className="push__cancel" onClick={this.handleClick} required/>
                <div className="push__create">Create a new Advert</div>
                <form onSubmit={this.handleCreateAdvert}>
                    <div className="push__container">
                        <label htmlFor="push__file" className="push__label">Image size: 350 by 700
                        <input type="file" name="advertImage" value={adverts.name} placeholder="Add Picture" className="push__input" onChange={this.handleImageChange} id="push__file" required  style={{ display: 'none'}}/>
                        <div id="push__file" className="push__input">
                            <img src={this.state.adverts.image.imagePreviewUrl} value={this.state.adverts.image.advertImage} alt='Img' onLoad={this.onImgLoad}  />
                        </div>
                        </label>
                    </div>
                    <div className="push__container">
                        <label htmlFor="link" className="push__label">Enter Link</label>
                        <input type="text" name="link"  value={adverts.link} placeholder="https://www.figma.com/file/kj..." onChange={this.handleChange} className="push__input" required/>
                    </div>
                    <div className="push__container">
                        <label htmlFor="title" className="push__label">Advert Title</label>
                        <input type="text" name="title"  value={adverts.title} placeholder="Travelling this summer?" onChange={this.handleChange} className="push__input" required/>
                    </div>
                    <div className="push__container">
                        <label htmlFor="module" className="push__label">Select Module</label>
                        <select type="text" name="module" value={adverts.module} placeholder="Travelling this summer?"className="push__input" onChange={this.handleChange} id="push__select" required>
                            <option value="select">Select a module</option>
                            <option value="mybank">mybank</option>
                            <option value="atease">atease</option>
                            <option value="pension">pension</option>
                            <option value="mutual">mutual</option>
                            <option value="insurance">insurance</option>
                        </select>
                    </div>
                    <div className="push__container">
                        <label htmlFor="text" className="push__label">Advert Text</label>
                        <textarea placeholder="Get insured today" name="text"  value={adverts.text}className="push__input" onChange={this.handleChange} required/>
                    </div>
                    <div className="push__contained"> 
                        <button className="push__push">Push</button>
                        <span className="push__span">or</span> 
                        <button className="push__cancelbtn" onClick={this.handleClick}>Cancel</button>
                    </div>
                </form>
            </div>
        </div>
        </div>
    </div>
        )
    }
} 
 
const mapStateToProps = state => ({
    searchAllDevice: state.stanbic.searchDevice
})

export default withRouter(
    connect(
        mapStateToProps,
        {
           createAdvert
        }
    )(Manage)
)