import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../layout/Spinner';
import {
    getAllNotifications,
} from "../../store/api"
import PushNotificationRow from './PushNotificationRow';
import BlueArr from "../../images/bluearr.svg";
import BackArrow from "../../images/Back.svg";

class PushNotificationTable extends Component {
    state = {
        query: "",
        data: [],
        firstPage: 1,
        currentPage: 1,
        usersPerPage: 5,
        loading: false,
        filteredData: [],
        accountData: [
            {   
                id: '1',
                new: "New offer for you!",
                range: "Our wide range of personal business and banking solutions..",
                link: "www.figma.com/file/KjQBJ",
                date: "03/11/2019",
                
            },
            {   
                id: '2',
                new: "Well Done Peter!",
                range: "Our wide range of personal business and banking solutions..",
                link: "www.figma.com/file/KjQBJ",
                date: "03/11/2019",
                
            },
            {
                id: '3',
                new: "New offer for you!",
                range: "Our wide range of personal business and banking solutions..",
                link: "www.figma.com/file/KjQBJ",
                date: "03/11/2019",
            },
            {
                id: '4',
                new: "Well Done Peter!",
                range: "Our wide range of personal business and banking solutions..",
                link: "www.figma.com/file/KjQBJ",
                date: "03/11/2019",
            },
        ]
    }
    componentDidMount = () => {
        this.setState({ loading: true })
        this.props.getAllNotifications()
    }
    componentWillReceiveProps(nextProps) {
        if((this.state.loading) && (this.props.getNotification !== null)) {
            this.setState({
                loading: false
            })
        }
    }
    handlePagnationUp =(e) =>{
        this.setState((prevState, props) => {
          if (this.state.currentPage >= this.state.firstPage) {
            return { currentPage: prevState.currentPage + 1 }
          }
        })
        e.target.blur()
     
      }
      handlePagnationDown =(e) => {
        this.setState((prevState, props) => {
          return { currentPage: prevState.currentPage - 1 }
        })
        e.target.blur()
      }
    handleInputChange = e => {
        const query = e.target.value;
        this.setState(prevState => {
            const filteredData = this.state.accountData.filter(obj => {
                return obj.new.toLowerCase().includes(query.toLowerCase());
            })
            return {
                query,
                filteredData
            };
        });
    }
    render() {
        const {loading} = this.state;
        const _content = this.props.getData
        console.log(_content)
        const indexOfLastUser = this.state.currentPage * this.state.usersPerPage;
        const indexOfFirstUser = indexOfLastUser - this.state.usersPerPage;
        const currentUsers = _content.slice(indexOfFirstUser, indexOfLastUser);

        const account = currentUsers.map((table) => (
            <PushNotificationRow 
                value={table}
                key={table._id}
            />
        ))
        if(loading) return <Spinner />  
        return (
            <div className="account__bold">
                <table className="account__table">
                    <thead className="account__tableheader">
                        <tr className="account__head">
                            <th className="account__first" id="account__first">All Push Notifications</th>
                            <th className="account__first">&nbsp;</th>
                            <th className="account__first">&nbsp;</th>
                        </tr>
                    </thead>
                    <tbody>
                        {account}
                    </tbody>
                </table>
                <div id='content__numbers'>
             {/* <span> Showing {newindexOfFirstUser} - {pageUsers} of {_content.length }  </span> */}
             <div className="account__top">
               {this.state.currentPage !== 1 ? <img src={BackArrow} alt="BackArrow" onClick={this.handlePagnationDown}id="account__first"/> : null}
               <span> {this.state.currentPage} / {Math.ceil(_content.length / this.state.usersPerPage)} </span>
               {this.state.currentPage >= Math.ceil(_content.length / this.state.usersPerPage) ? null : <img src={BlueArr} alt="Arrow" onClick={this.handlePagnationUp} id="account__blue" />}
             </div>
        </div>
    
            </div>
        )
    }
}


const mapStateToProps = state => ({
    getNotification: state.stanbic.getPushNotifications
})

export default withRouter(
    connect(
        mapStateToProps,
        {
           getAllNotifications
        }
    )(PushNotificationTable)
)

