import React, {Component} from 'react';
import Navbar from '../layout/Navbar';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import {
    getAdvert
  } from "../../store/api"

class ViewAdvert extends Component {

    render () {
        const { getOneAdvert} = this.props;
        const advertContent = [...[getOneAdvert]]
        const account = advertContent.map((advert, key) => 
           <div key={key} className="account__advert">
               <div>
                <span><img src={advert.advertImage} alt="UserImage" /></span>
               </div>
                <div className="account__board">
                    <div className="account__til">{advert.title}</div>
                    <div className="account__tex">{advert.text}</div>
                    <div className="account__rem">Remove</div>
                </div>
               
           </div>
        );
        return (
            <div>
                <Navbar />
                <div className="cards__cover">
                    <div className="push__notify">Manage Advert</div>
                        
                            {/* <img src={UserIcon} alt="User" className="device__image" /> */}
                            {account}
                     
        
                    
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    getOneAdvert: state.stanbic.advert
      
})
export default withRouter(
    connect(
        mapStateToProps,
        {
          getAdvert
        }
    )(ViewAdvert)
)