import types from "../types"

const { 
    GET_USERS,
    GET_USER,
    SEARCH_DEVICE,
    CREATE_ADVERT,
    GET_ADVERTS,
    GET_ADVERT,
    UNLINK_DEVICE,
    PUSH_SINGLE_NOTIFICATION,
    PUSH_ALL_NOTIFICATION,
    GET_PUSH_NOTIFICATIONS,
    LOGIN
} = types

const initialState = {
    users: [],
    user: [],
    searchDevice: [],
    createAdvert: [],
    adverts: [],
    advert: [],
    unlinkDevice: [],
    pushSingleNotification: [],
    pushAllNotification: [],
    getPushNotifications: [],
    login:[]
};

export default (state = initialState, action) => {
    switch (action.type) {
        case GET_USERS:
        return {
            ...state,
            users: action.payload
        } 
        case GET_USER:
        return {
            ...state,
            user: action.payload
        } 
        case SEARCH_DEVICE:
        return {
            ...state,
            searchDevice: action.payload
        } 
        case CREATE_ADVERT:
        return {
            ...state,
            createAdvert: action.payload
        } 
        case GET_ADVERTS:
        return {
            ...state,
            adverts: action.payload
        } 
        case GET_ADVERT:
        return {
            ...state,
            advert: action.payload
        } 
        case UNLINK_DEVICE:
        return {
            ...state,
            unlinkDevice: action.payload
        } 
        case PUSH_SINGLE_NOTIFICATION:
        return {
            ...state,
            pushSingleNotification: action.payload
        } 
        case PUSH_ALL_NOTIFICATION:
        return {
            ...state,
            pushAllNotification: action.payload
        } 
        case GET_PUSH_NOTIFICATIONS:
        return {
            ...state,
            getPushNotifications: action.payload
        } 
        case LOGIN:
        return {
            ...state,
            login: action.payload
        } 
        default:
            return state;
    }
};