import types from "../types";

const { 
    GET_USERS,
    GET_USER,
    CREATE_ADVERT,
    GET_ADVERTS,
    GET_ADVERT,
    PUSH_SINGLE_NOTIFICATION,
    PUSH_ALL_NOTIFICATION,
    GET_PUSH_NOTIFICATIONS,
    LOGIN
} = types


export const getAllUsers = data => ({
    type: GET_USERS,
    payload: data
})
export const getAllUser = data => ({
    type: GET_USER,
    payload: data
})
export const searchDevice = data => ({
    type: GET_USERS,
    payload: data
})
export const createAllAdvert = data => ({
    type: CREATE_ADVERT,
    payload: data
})
export const getAllAdverts = data => ({
    type: GET_ADVERTS,
    payload: data
})
export const getAllAdvert = data => ({
    type: GET_ADVERT,
    payload: data
})
export const unlinkDevices = data => ({
    type: GET_USERS,
    payload: data
})
export const pushSingleNotification = data => ({
    type: PUSH_SINGLE_NOTIFICATION,
    payload: data
})
export const pushAllNotification = data => ({
    type: PUSH_ALL_NOTIFICATION,
    payload: data
})
export const getPushNotifications = data => ({
    type: GET_PUSH_NOTIFICATIONS,
    payload: data
})
export const logIN = data => ({
    type: LOGIN,
    payload: data
})