import React, { Component } from 'react';
import { Link, withRouter} from 'react-router-dom';
import UserImage from '../../images/UserImage.png';
import Logo from '../../images/icon.svg';
import Arrow from '../../images/downarr.svg';

class Navbar extends Component {
    state = {
        dropdown: false
    }
    
    handleDropdown = () => {
        const {dropdown} = this.state;
       this.setState({ dropdown: !dropdown })
    }
    gotoAdmin = () => {
        console.log('hey')
        this.props.history.push('/admin')
    }
    gotoHome = () => {
        this.props.history.push('/')
    }
    render () {
        const { dropdown } = this.state
        return (
            <nav className="navbar__cover">
                <div className="navbar__iconmain">
                    <div className="navbar__head">{this.props.heading}</div>
                    {/* <img src={this.props.icon} alt="Logo" onClick={this.gotoHome} className="navbar__icon" /> */}
                </div> 
                <ul className="navbar__list">
                    <li className="navbar__link">
                        <Link to="/" className="navbar__text">{this.props.user}</Link>
                        <img src={this.props.arrow} alt="Arrow" className="navbar__arrow" onClick={this.handleDropdown} />
                    </li>
                    {
                    dropdown ? <div className="navbar__position">
                         <div className="cards__cardbox">
                            <div className="cards__displayadmin">
                                <img src={UserImage} alt="Logo" className="cards__picture"/>
                                <div>
                                    <div className="cards__name">Chidinma</div>
                                    <div className="cards__little">chidinma@swipemax.com</div>
                                </div>
                            </div>
                            <div className="cards__coverbtns" >
                                <div >
                                    <button className="cards__management" onClick={() =>this.gotoAdmin()}>Admin Management</button>
                                </div>
                                <div>
                                    <button className="cards__logout">Logout</button>
                                </div>
                               
                            </div>
                        </div>
                                    </div>: null
                }
                </ul>
                
            </nav>
        )
    }
    
}
Navbar.defaultProps = {
    user: "Anuigbo Chidinma",
    icon: Logo,
    arrow: Arrow
}

export default withRouter(Navbar)
