import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './components/pages/Home';
import Login from './components/login/Login';
import PushNotification from './components/pages/PushNotification';
import DeviceBinding from './components/pages/DeviceBinding';
import Transaction from './components/pages/Transaction';
import ManageAdvert from './components/pages/ManageAdvert';
import Admin from './components/pages/Admin';
import NewPush from './components/pushNotification/NewPush';
import NewAdmin from './components/admin/AdminNew';
import NewAdvert from './components/manageAdverts/Manage';
import ViewTransaction from './components/transaction/ViewTransaction';
import ViewDevice from './components/deviceBinding/ViewDevice';
import ViewAdvert from './components/manageAdverts/ViewAdvert';
import './scss/main.scss';

const App = () => {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path="/login" component={Login}/> 
          <Route exact path="/" component={Home}/>
          <Route exact path="/pushnotification" component={PushNotification}/>
          <Route exact path="/newpost" component={NewPush}/>
          <Route exact path="/newadmin" component={NewAdmin}/>
          <Route exact path="/newadvert" component={NewAdvert}/>
          <Route exact path="/devicebinding" component={DeviceBinding}/>
          <Route exact path="/transaction" component={Transaction}/>
          <Route exact path="/manageadvert" component={ManageAdvert}/>
          <Route exact path="/admin" component={Admin}/>
          <Route exact path="/viewdevice" component={ViewDevice}/>
          <Route exact path="/viewtransaction" component={ViewTransaction}/>
          <Route exact path="/viewadvert" component={ViewAdvert}/>
        </Switch>
      </div>
    </Router>
   
  );
}

export default App;
